.img-banner{
    width: 100%;
    min-height: 300px;
    max-height: 350px;
}

.box-info{
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center; 

}

.box-info texto {
    
}

.cab{
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    color: #fff;
}

.finan{
    background: rgb(13, 187, 36);
    background: linear-gradient(90deg, rgb(25, 68, 31) 0%, rgb(30, 150, 26) 35%, rgb(6, 211, 57) 100%);
    color: #fff; 
}

.carousel-inner {
    max-height: 100vh;
    max-width: 100vh;
}

.carousel-inner img {
    max-height: 100vh;
    max-width: 100vh;
}


.btn-editar i{
    position: fixed;
    bottom: 10px;
    right: 10px;
    color: #f21667;
    transition: .3s ease-in;
}

.btn-editar i:hover{
    color: #614ea6;
}

.titulo, .visualiacoes{
    color: #614ea6;
}

.GB {
    color: black;
    background: white;
}