.cor {
  background: #f0f0fa;
}

.cor2 {
  background-color: rgb(245, 245, 245);
}

.text-blue {
  color: blue;
}

.text-blue:hover {
  color: blue;
}

.textoHome {
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  font-size: 25px;
}

.player {
  padding-top: 10px;
  padding-bottom: 10px;
  box-sizing: border-box;
  border-radius: 10px;
  color: #000;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  -msn-background-size: cover;
}

.tituloHome {
  font-weight: bold;
  font-size: 18px;
  font-style: italic;
  background-color: #000;
  padding: 0;
  margin: 0;
  color: #FFF;
}

@media (max-width: 600px) {
  .video {
    padding: 0;
    margin: 0;
    padding-left: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.videoWeb {
  background-color: #000;
  border-radius: 15px;
}

.t {
  font-weight: bold;
  padding-top: 5px;
}

.box {
  width: 640px;
  height: 370px;
  background-color: #000;
}

.superFont {
  font-size: 88px;
}

.w2 {
  background-color: black;
  border-radius: 15px;
  padding: 20px;
}

.w3 {
  font-size: 28px;
}