html {
  /** Reseta os estilos **/
  margin: 0;
  padding: 0;
  background: url("http://a.top4top.net/p_397znzmu1.png") center center fixed;
  color: #000;
  height: 100vh;
  position: relative;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  -msn-background-size: cover;
}

.card + div {
  margin-left: 10px;
}

body {
  background: url("http://a.top4top.net/p_397znzmu1.png") center center fixed;
  color: #000;
  height: 100vh;
  position: relative;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  -msn-background-size: cover;
}

.overlay{
  background: rgba(0, 0, 0, 0.58);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0; 
}

div.body-content{
  /** Essa margem vai evitar que o conteudo fique por baixo do rodapé **/
  margin-bottom: 60px;
}

.btn-cadastro{
  background-color: #614EA6;
  color: #fff;
}

.btn-cadastro:hover{
  background-color: #F21667;
  color: #fff;
}

.search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.centro {
  display: flex;
  justify-content: center;
  align-items: center;
}

.f-component {
  font-size: 30px;
}

.form {
  background: #f0f0fa;
  color: #000;
  border-radius: 15px;
  margin: 10px;
  font-size: 25px;
  padding: 10px;
}

.mini-card + Link {
  margin-left: 5px;
}

.mini-card + .mini-card {
  margin-left: 5px;
}

.pcp-card {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  max-width: 100%;
}

.pcp-card + div + h5 {
  margin-right: 10px;;
}

.detalhes {
  background-color: rgba(83, 77, 79, 0.2);;
  border-radius: 15px;
  color: #000;
  border-radius: 15px;
}

.semDeco {
  text-decoration: none;
}

.semDeco:hover {
  text-decoration: none;
}

.datascreen {
  background: rgb(226, 222, 222);
  border-radius: 15px;
}

.btnfixed {
  position: absolute;
  bottom: 10px;
  right: 55px;
}

.btnfixed2 {
  position: absolute;
  bottom: 70px;
  right: 80px;
}