.img-banner{
    width: 100%;
    min-height: 300px;
    max-height: 750px;
}

.box-info{
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
    color: #fff;
    display: flex;
    display: flex;
    flex-direction: column;
    align-items: center; 

}

.btn-editar i{
    position: fixed;
    bottom: 10px;
    right: 10px;
    color: #f21667;
    transition: .3s ease-in;
}

.btn-editar i:hover{
    color: #614ea6;
}

.titulo, .visualiacoes{
    color: #614ea6;
}

.carousel-inner {
    max-height: 100vh;
    max-width: 100vh;
}

.carousel-inner img {
    max-height: 100vh;
    max-width: 100vh;
}