.login-content {
    width: 100%;
    padding: 15%;
    margin: auto;
    height: 100vh;
}

.login-content-web {
    width: 100%;
    padding: 15%;
    margin: auto;
    height: 100vh;
}

.btn-login {
    background-color: #fff;
    color: #000;
}

.btn-login:hover {
    background-color: #0011ff;
    color: #fff;
}

.opcoes-login a{
    text-decoration: none;
    background-color: #fff;
    color: #000;
    transition: .5 ease-in;
    padding: 5px;
    border-radius: 10px;
    font-size: 20px;
    border-style: groove;
    border-color: #0011ff;
}

.opcoes-login a:hover{
    text-decoration: none;
    background-color: #0011ff;
    border-color: #FFF;
    border-style: groove;
    color: #FFF;
    font-size: 20px;
}

.login {
    padding: 15px;
    border-radius: 10px;
    background: #000 center;
    opacity: 0.8;
}

.login-web {
    padding: 15px;
    margin: 15px;
    border-radius: 10px;
    background: #000 center;
    opacity: 0.8;
    font-size: 30px;
}