.btn-cadastro{
  background-color: #614EA6;
  color: #fff;
}

.btn-cadastro:hover{
  background-color: #F21667;
  color: #fff;
}

.cabecalho {
  font-size: large;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-color: brown;
  color: white;
}

.cabecalho-1 {
  background-color: green;
  color: white;
}

.cabecalho-2 {
  font-size: large;
  background-color: greenyellow;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: black;
}

.total-final {
  padding: 10px;
  background-color: green;
  color: white;
  height: 100%;
  font-size: large;
}

.altura-100 {
  height: 100%;
}

.cabe {
  font-size: large;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: white;
  margin: auto;
  background: rgb(2,0,36);
  background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgb(0, 0, 0) 35%, rgb(212, 216, 208) 100%);
}

.centralizado {
  display: flex;
  align-items: center;
  justify-content: center;
}

.aviso{
  margin: 10px;
  padding: 15px;
  background-color: #f0f0fa;
}

.form-orc {
  background: #f0f0fa;
  color: #000;
  border-radius: 15px;
  margin: 10px;
  opacity: 0.9;
  font-size: 15px;
  font-weight: bold;
}