@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap');

.navbar{
    background:black;
    border-radius: 2px;
    color: white;
}


.dropdown-toggle {
    color: #fff;
}

.dropdown-menu {
    background: #fff;
}

.dropdown-menu a{
    padding: 5px;
    color: #000;
}

.dropdown-toggle:hover {
    color: #fff;
}

.user {
    width: 100%;
    padding: 15px;
    margin: 5px;
    background-color: #FFF;
    color: #000;
    font-weight: bold;
    border-style: solid #FFF;
    border-radius: 10px;

}

.user:hover {
    background-color: rgb(0, 0, 0);
    color: #FFF;
    font-weight: bold;
    border-style: solid #FFF;
    border-radius: 10px;
}

.cor3 {
    background-color: rgb(255, 0, 0);
}

.exit {
    background-color: rgb(255, 0, 0);
    font-size: 22px;
    color: #FFF;
    font-weight: bold;
    border-radius: 20px;
    margin-left: 3px;
}

.exit:hover {
    background-color: rgb(143, 46, 46);
    color: #FFF;
    font-weight: bold;
    border-radius: 10px;
    margin-left: 3px;
}

.admin {
    font-size: 22px;
    background-color: #008000;
    color: #FFF;
    font-weight: bold;
    border-radius: 20px;
    margin-left: 3px;
}

.admin:hover {
    background-color: rgb(63, 133, 80);
    color: #FFF;
    font-weight: bold;
    border-radius: 10px;
    margin-left: 3px;
}

.nav-link {
    background: #fff;
    text-decoration: none;
    list-style: none;
}

.menuDrop {
    border: 4px;
    border-style: double;
    margin-left: 4px;
    border-radius: 10px;
    color: #000;
    font-weight: bold;
    margin-right: 4px;
    background: #FFF;
    border-color: #000;
    font-size: 22px;
}

.menuIcon {
    font-size: 14px;
    border: 4px;
    border-style: double;
    margin-left: 4px;
    border-radius: 10px;
    color: #000;
    font-weight: bold;
    margin-right: 4px;
    background: #FFF;
    border-color: #000;
}

.menuIcon:hover {
    border: 4px;
    width: 100%;
    font-size: 15px;
    border-style: double;
    margin-left: 4px;
    border-radius: 10px;
    color: #FFF;
    font-weight: bold;
    margin-right: 4px;
    background: #000;
    -webkit-font-smoothing: antialiased;
    font: 400 15px Roboto, sans-serif;
}

.menuDrop:hover {
    border: 4px;
    font-size: 15px;
    border-style: double;
    margin-left: 4px;
    border-radius: 10px;
    color: #FFF;
    font-weight: bold;
    margin-right: 4px;
    background: #000;
    -webkit-font-smoothing: antialiased;
    font: 400 26px Roboto, sans-serif;
}

@media (max-width: 600px) {
    .menuPhone {
        margin-top: 10px;
        border-radius: 10px;
        background-color: #FFF;
        padding: 15px;
    }
    #user {
        background-color: #000;
        color: #FFF;
    }
}

.c {
    display: flex;
    justify-content: center;
    align-items: center;
}

.subItens {
    font-size: 22px;
}

.welcome {
    height: 100vh;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
}