* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap');

body {
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
    font: 400 14px Roboto, sans-serif;
}

card {
    font: 400 14px Roboto, sans-serif;
    background: #fff;
    -webkit-font-smoothing: antialiased;
    display: flex;
    align-items: center;
}